import React from "react";
import Slider from "react-slick";
import "./Carousel.css";

export default class SimpleSlider extends React.Component {
  render() {
    var settings = {
      className: "center",
      centerMode: true,
      infinite: true,
      centerPadding: "5px",
      slidesToShow: 3,
      speed: 500,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            infinite: true,
            dots: true
          }
        },
        {
          breakpoint: 600,
          settings: {
            slidesToShow: 3,
            slidesToScroll: 3,
            initialSlide: 1,
            centerPadding: "5px"
          }
        },
        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerPadding: "1px"
          }
        }
      ]
    };
    return (
      <div className="container">
        <Slider {...settings}>
          <div>
            <img
              src="http://placekitten.com/g/400/200"
              width="200"
              height="100"
            />
          </div>
          <div>
            <img
              src="http://placekitten.com/g/400/200"
              width="200"
              height="100"
            />
          </div>
          <div>
            <img
              src="http://placekitten.com/g/400/200"
              width="200"
              height="100"
            />
          </div>
          <div>
            <img
              src="http://placekitten.com/g/400/200"
              width="200"
              height="100"
            />
          </div>
        </Slider>
      </div>
    );
  }
}
